<script>
export default {
  data() {
    return {
      MO_email: "",
      MO_reason_content: "",
    };
  },
  methods: {
    async outMember() {
      try {
        if (this.MO_email == "") {
          this.$swal("!", " ", "question");
        } else if (this.MO_reason_content == "") {
          this.$swal("!", " ", "question");
        } else {
          const response = await this.$axios.post(`${this.$store.state.host}/app/guest/member/out`, {
            MO_email: this.MO_email,
            MO_reason: "6",
            MO_reason_content: this.MO_reason_content,
          });

          if (response.data.msg == " ") {
            this.$swal("Error", "   .", "error");
          } else {
            this.$swal("", "   .", "success");
          }
        }
      } catch (err) {
        console.log(err);
      }
    },
  },
};
</script>

