<template>
  <div>
    <div id="admin-main-box">
      <div>
        <span id="dashboard-main-title">   </span>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table-simple id="admin-store-table">
          <b-tr>
            <b-th style="width: 10%"></b-th>
            <b-th style="width: 20%"></b-th>
            <b-th style="width: 20%"></b-th>
            <b-th style="width: 20%"></b-th>
            <b-th style="width: 20%"></b-th>
            <b-th style="width: 10%"></b-th>
          </b-tr>
          <b-td> - </b-td>
          <b-td>
            <b-form-input v-model="AM_id" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_pw" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_name" />
          </b-td>
          <b-td>
            <b-form-input v-model="AM_rank" />
          </b-td>
          <b-td>
            <b-button
              variant="warning"
              style="font-weight: bold"
              @click="saveNotice"
            >
              
            </b-button>
          </b-td>
        </b-table-simple>
      </div>
      <div id="admin-dashboard-borad-top">
        <b-table
          :items="adminMemberList"
          :per-page="perPage"
          :current-page="currentPage"
          :fields="fields"
          id="admin-notice-table"
        >
          <template #cell(AM_idx)="data">
            <b id="mypage-orderNumer-fontcolor">{{ data.value }}</b>
          </template>
          <template #cell()="row">
            <b-form-input v-model="row.item.AM_pw" />
          </template>
          <template #cell(AM_rank)="row">
            <b-form-input v-model="row.item.AM_rank" />
          </template>
          <template #cell(AM_reg_date)="row">
            {{ row.item.AM_reg_date | yyyyMMdd }}
          </template>

          <template #cell()="row">
            <b-button
              size="sm"
              variant="primary"
              @click="editBtn(row.item)"
              id="board_edit_btn"
            >
              
            </b-button>
          </template>
          <template #cell()="row">
            <b-button
              size="sm"
              variant="danger"
              @click="deleteBtn(row.item)"
              id="board_edit_btn"
            >
              
            </b-button>
          </template>
        </b-table>

        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="admin-notice-table"
          id="board-pagination"
        ></b-pagination>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      AM_id: "",
      AM_pw: "",
      AM_name: "",
      AM_rank: "",
      adminMemberList: [],
      fields: [
        { key: "AM_idx", label: "", thClass: "w5" },
        { key: "AM_id", label: "", thClass: "w20" },
        "",
        { key: "AM_name", label: "", thClass: "w15" },
        { key: "AM_rank", label: "", thClass: "w15" },
        { key: "AM_reg_date", label: "", thClass: "w15" },
        "",
        "",
      ],
      perPage: 9,
      filter: null,
      currentPage: 1,
    };
  },
  created() {
    this.$store.dispatch("userStore/getAuthMemberinfo").then(() => {
      if (this.$store.state.userStore.AM_idx == "") {
        this.$swal("Warning", "   .", "warning");
        this.$router.push("/");
      }
    });
    this.getAdminMemberList();
  },
  computed: {
    rows() {
      return this.adminMemberList.length;
    },
  },
  methods: {
    async getAdminMemberList() {
      try {
        const response = await this.$axios.get(
          `${this.$store.state.host}/admin/list/data`
        );
        this.adminMemberList = response.data;
      } catch (err) {
        console.log(err);
      }
    },
    async saveNotice() {
      if (this.AM_id == "") {
        this.$swal("!", "  ", "question");
        return;
      }
      if (this.AM_pw == "") {
        this.$swal("!", "  ", "question");
        return;
      }
      if (this.AM_name == "") {
        this.$swal("!", "  ", "question");
        return;
      }
      if (this.AM_rank == "") {
        this.$swal("!", "  ", "question");
        return;
      }
      const response = await this.$axios.post(
        `${this.$store.state.host}/admin/list/create`,
        {
          AM_id: this.AM_id,
          AM_pw: this.AM_pw,
          AM_name: this.AM_name,
          AM_rank: this.AM_rank,
        }
      );
      if (response.data.msg == "  .") {
        this.$swal("Error", "  .", "error");
      } else {
        this.$swal("Success", " .", "success");
        this.AM_id = "";
        this.AM_pw = "";
        this.AM_name = "";
        this.AM_rank = "";
        this.getAdminMemberList();
      }
    },
    async editBtn(item) {
      try {
        await this.$axios.post(`${this.$store.state.host}/admin/list/edit`, {
          AM_idx: item.AM_idx,
          AM_pw: item.AM_pw,
          AM_rank: item.AM_rank,
        });
        this.$swal("Success", " .", "success");
        this.getAdminMemberList();
      } catch (err) {
        console.log(err);
      }
    },
    async deleteBtn(item) {
      this.$swal({
        title: "Warning",
        text: "?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "",
        cancelButtonText: "",
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            await this.$axios.post(
              `${this.$store.state.host}/admin/list/delete`,
              { AM_idx: item.AM_idx }
            );
            this.getAdminMemberList();
            this.$toast.success("");
          } catch (err) {
            console.log(err);
            this.$toast.error(".");
          }
        }
      });
    },
  },
};
</script>

<style></style>
